.Works {
    padding: 60px 0;
    // width: 90%;
    height: 100%;
    box-sizing: border-box;
    margin: 0 auto;
    animation: pageLoad 1s;
    background:#2a292f
}

@keyframes pageLoad {
    from {
        opacity: 0;
        transform: scale3d(0.5, 0.5, 0.5);
    }
    to {
        opacity: 1;
        transform: scale3d(1, 1, 1);
    }
}
